body
{
  font-family: system-ui;
  display: flex;
  justify-content:space-between;
  
}

h1,h2,h3,h3,h4,h6
{
  margin: 0;
  color: rgba(0,0,0,0.8);
}

th
{
  color: rgba(0, 0, 0,0.5);
}


#sub_Title
{
  margin-top: 25px;
}

#FormulaEdit
{
  margin-top: 20px;
}

#FormulaEdit > input
{
  width: 35vw;
  height: 30px;
  outline: none;
  padding: 0 8px 0 8px;
  font-weight: 600;
  letter-spacing: 0.05em;
  border: 2px solid rgba(0, 0, 0,0.5);
  margin-top: 10px;
  border-radius: 3px;
}

#FormulaEdit > input:focus
{
  border-color: rgb(82, 171, 245);
  box-shadow: 0 0 8px 1px rgb(82, 171, 245);
}

#FormulaEdit > h4
{
  margin-top: 10px;
}

#submit_formula
{
  background-color: rgb(20, 84, 202);
  border:none;
  color: white;
  font-weight: 600;
  font-size: 1em;
  border-radius: 4px;
  margin-top: 15px;
  width: 135px;
  height: 35px;
  cursor: pointer;
}

#cancel_formula
{
  background-color: transparent;
  border:none;
  color: rgb(17, 16, 16);
  font-weight: 600;
  display: none;
  border:  2px solid rgba(0,0,0,0.8);
  font-size: 1em;
  border-radius: 4px;
  margin-top: 15px;
  width: 100px;
  height: 35px;
  margin-left: 20px;
  cursor: pointer;
} 

#App
{
  margin-left: 10vw;
}

#data_frml
{
  width: 100vw;
  font-family: system-ui;
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
}

#ViewUser
{
  width: calc(100vw - 39vw);
  height: 350px;
  margin-top: -45px;

}

#ViewUser h3
{
  font-size: 1.75em;
}

td
{
  text-align: center;
}

#table_action
{
  width: 450px;
  border-collapse: collapse;
}

#table_action  th
{
  height: 30px;
  width: 0px;
  border-bottom: 2px solid rgba(0, 0, 0,0.5);
}


#table_action  td
{
  height: 45px;
  border-bottom: 2.5px solid rgba(0, 0, 0,0.25);
}

.DivAction
{
  display: flex;
  justify-content: center;
}

.DivAction > button
{

  width: 60px;
  height: 30px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.404);
  background-color: transparent;
  margin-left: 30px;
}

.DivAction > button:hover
{
  border-color: rgb(82, 171, 245);
  box-shadow: 0 0 3px 1px rgb(82, 171, 245);
}

#table_action tr
{
  height: 30px;
}